import React from 'react';
import classname from 'classnames';

export enum ButtonVariant {
    default = 'default',
    primary = 'primary',
    secondary = 'secondary',
    success = 'success',
    info = 'info',
    danger = 'danger',
    warning = 'warning',
}

export enum ButtonSize {
    sm,
    md,
    lg,
}

export interface IProps {
    outlined?: Boolean;
    onClick?: () => void;
    disabled?: Boolean;
    label: string,
    variant: ButtonVariant
    size: ButtonSize
}

export const Button: React.FC<IProps> = ({ 
    label,
    outlined = false,
    size = ButtonSize.md,
    variant = ButtonVariant.default,
    ...props
 }) => {
    return (
        <button className={
            classname({
                'font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg mr-1 mb-1 ease-linear transition-all duration-150' :  true,
                'bg-blueGray-500 text-white active:bg-blueGray-600' : ButtonVariant.default == variant,
            })
        }>
            {label}
        </button>
    )
}